class Validator {
    static validarCPF(cpf) {
      cpf = cpf.replace(/[^\d]+/g, ''); // Remove caracteres no numricos
  
      // Verifica se o CPF possui 11 dgitos ou se todos os dgitos so iguais
      if (cpf.length !== 11 || /^(\d)\1+$/.test(cpf)) {
        alert('CPF invlido!');
        return false;
      }
  
      var soma = 0;
      var resto;
  
      // Calcula o primeiro dgito verificador
      for (var i = 1; i <= 9; i++) {
        soma += parseInt(cpf.substring(i - 1, i)) * (11 - i);
      }
      resto = (soma * 10) % 11;
  
      if ((resto === 10) || (resto === 11)) {
        resto = 0;
      }
      if (resto !== parseInt(cpf.substring(9, 10))) {
        alert('CPF invlido!');
        return false;
      }
  
      soma = 0;
  
      // Calcula o segundo dgito verificador
      for (i = 1; i <= 10; i++) {
        soma += parseInt(cpf.substring(i - 1, i)) * (12 - i);
      }
      resto = (soma * 10) % 11;
  
      if ((resto === 10) || (resto === 11)) {
        resto = 0;
      }
      if (resto !== parseInt(cpf.substring(10, 11))) {
        alert('CPF invlido!');
        return false;
      }
  
      return true; // CPF vlido
    }
}

export default Validator; 